body {
  background-color: #f5f5f5;
  font-family: "Courier New", Courier, monospace-;
}
.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 3rem;
}

.navigation {
  margin-top: 0px;
  font-weight: 200;
}

.nav-ul {
  list-style-type: none;
  margin: 0;
  padding: 15px;
  overflow: hidden;
  border-bottom: 2px solid #242422;
  background-color: #e9e8e8;
}

.navigation ul li {
  float: left;
}

.nav-link {
  display: block;
  color: #666;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.nav-link:hover:not(.active) {
  background-color: rgb(194, 194, 194);
}
.header {
  font-size: 25pt;
  margin-bottom: 50px;
}
.count {
  margin-bottom: 2rem;
  text-align: center;
  display: flex;
  margin-left: 5rem;
}
.count h3 {
  line-height: 8.6rem;
  margin-right: 1rem;
}
.count-display {
  font-size: 5em;
  margin-top: 2rem;
}
.buttons {
  margin-top: 40px;
}
.app .buttons button {
  width: 10rem;
  height: 10rem;
  font-family: "Courier New", Courier, monospace-;
  border-radius: 50%;
  border: none;
  margin: 15px;
  font-size: 5em;
  box-shadow: 0px 2px 3px 2px #242422;
  cursor: pointer;
}
.app .buttons button:hover {
  font-size: 7em;
}
.app .buttons button:focus {
  outline: 0;
}
::placeholder {
  font-family: "Courier New", Courier, monospace-;
  padding-left: 5px;
}
.setValue {
  border-radius: 10px;
  height: 50px;
}

.app .buttons reset-button button:hover {
  font-size: 9em;
}
