body {
  background-color: #f5f5f5;
  font-family: "Courier New", Courier, monospace-;
}

.navigation {
  margin-top: 0px;
  font-weight: 200;
}

.nav-ul {
  list-style-type: none;
  margin: 0;
  padding: 15px;
  overflow: hidden;
  border-bottom: 2px solid #242422;
  background-color: #e9e8e8;
}

.navigation ul li {
  float: left;
}

.nav-link {
  display: block;
  color: #666;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.nav-link:hover:not(.active) {
  background-color: rgb(194, 194, 194);
}

.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 40px;
}
.home-div {
  width: 100%;
  max-width: 400px;
  min-width: 200px;
  box-shadow: 0px 3px 10px 2px #242422;
  border-radius: 15px;
  padding: 30px;
}

.home-page-header {
  font-size: 22pt;
}

.home-page-para {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 18pt;
  font-weight: 500;
}

.img_tag {
  height: 500px;
  min-height: 200px;
  max-height: 300px;
  margin-left: 100px;
}
