* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f5f5f5;
  font-family: "Courier New", Courier, monospace-;
}
.error-page-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 100px;
}
.error-page {
  width: 100%;
  max-width: 500px;
  min-width: 200px;
  box-shadow: 0px 3px 10px 2px #242422;
  border-radius: 15px;
  padding: 30px;
}

error .error-page-header {
  font-size: 48pt;
}

.error-page-para {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 18pt;
  font-weight: 500;
}

.error-page-homelink {
  text-decoration: none;
  color: blue;
}
